<h1 id="nexus搭建maven私有仓库" tabindex="-1"><a class="header-anchor" href="#nexus搭建maven私有仓库" aria-hidden="true">#</a> Nexus搭建Maven私有仓库</h1>
<h2 id="私服介绍" tabindex="-1"><a class="header-anchor" href="#私服介绍" aria-hidden="true">#</a> 私服介绍</h2>
<div class="custom-container tip"><p class="custom-container-title">一句话概况</p>
<p>局域网内的仓库服务</p>
</div>
<p>私服是一种特殊的远程仓库，架设在<code>局域网内</code>的仓库服务，私服<code>代理广域网</code>上的远程仓库，供局域网内的Maven用户使用。
当Maven需要下架构件的时候，先从私服请求，如果私服上不存在构件，则从外部的远程仓库下载，并缓存在私服上，再为Maven的下载请求提供服务。一些<code>无法从外部仓库下载的构件</code>也能从本地上传到私服，供其他人使用。</p>
<p><img src="http://image.hualihai.cn/blog/61386de0-2148-4675-be8a-59537e37a367" alt="结构图"></p>
<h2 id="为什么要使用私服" tabindex="-1"><a class="header-anchor" href="#为什么要使用私服" aria-hidden="true">#</a> 为什么要使用私服</h2>
<ul>
<li>节省外网带宽</li>
<li>加速Maven构件</li>
<li>部署第三方构件，上传自己公司的jar包</li>
<li>有的公司不能访问外网</li>
</ul>
<p>通过建立自己的私服，降低中央仓库负荷，节省外网带宽，加速maven构件，自己部署构件等，从而更高效的使用maven。</p>
<h2 id="安装nexus" tabindex="-1"><a class="header-anchor" href="#安装nexus" aria-hidden="true">#</a> 安装Nexus</h2>
<p><a href="https://help.sonatype.com/repomanager3" target="_blank" rel="noopener noreferrer">Nexus Repository OSS<OutboundLink/></a>是一个免费的，世界上最受欢迎的构件存储库，是典型的<code>Java Web</code>应用，内嵌了<code>Jetty</code>容器。</p>
<p><a href="https://www.sonatype.com/nexus-repository-oss" target="_blank" rel="noopener noreferrer">官网下载地址<OutboundLink/></a>
解压安装包</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">tar</span> -xzvf nexus-3.15.2-01-unix.tar.gz
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><div class="custom-container warning"><p class="custom-container-title">WARNING</p>
<p>Detected execution as &quot;root&quot; user.  This is NOT recommended!</p>
</div>
<p>解压出来有两个文件夹：<code>nexus-3.15.2-01</code> AND <code>sonatype-work</code></p>
<p>nexus {start|stop|run|run-redirect|status|restart|force-reload}</p>
